@charset "UTF-8";
body {
  font-family: "Noto Sans JP", sans-serif;
  font-size: rem(16);
  font-weight: 400;
  color: #1e1e1e;
  font-style: normal;
}

/* 構築時のみ、管理バーを非表示にする */
#wpadminbar {
  display: none !important;
}

.u-desktop {
  display: block;
}
@media screen and (max-width: 767px) {
  .u-desktop {
    display: none;
  }
}

.u-mobile {
  display: none;
}
@media screen and (max-width: 767px) {
  .u-mobile {
    display: block;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 1200px) {
  html {
    font-size: 1.3333333333vw;
  }
}
@media screen and (max-width: 767px) {
  html {
    font-size: 16px;
  }
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}

.footer {
  background: #1d61af;
  padding-top: 3.125rem;
  padding-bottom: 5rem;
}
@media screen and (max-width: 767px) {
  .footer {
    padding-top: 2.1875rem;
    padding-bottom: 3.125rem;
  }
}

.footer__inner {
  padding-inline: 2.5rem;
}

.footer__nav {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2.75rem;
}
@media screen and (max-width: 767px) {
  .footer__nav {
    flex-wrap: wrap;
    row-gap: 0.75rem;
    column-gap: 2.5rem;
    max-width: 18.4375rem;
    margin-inline: auto;
  }
}

.footer__nav-link {
  font-family: "Murecho", serif;
  font-size: 1rem;
  font-weight: 400;
  color: #fff;
  position: relative;
}
@media screen and (max-width: 767px) {
  .footer__nav-link {
    font-size: 0.875rem;
  }
}

.footer__nav-link:nth-of-type(n + 2)::before {
  content: "";
  width: 2px;
  height: 0.9375rem;
  background: #aebfdc;
  position: absolute;
  top: 50%;
  left: -1.375rem;
  transform: translateY(-50%);
}
@media screen and (max-width: 767px) {
  .footer__nav-link:nth-of-type(n + 2)::before {
    left: -1.25rem;
  }
}

@media screen and (max-width: 767px) {
  .footer__nav-link.no-bar::before {
    display: none;
  }
}

.footer__copyright {
  display: block;
  margin-top: 2.5rem;
  width: fit-content;
  margin-inline: auto;
  font-family: "Montserrat", serif;
  font-size: 0.875rem;
  font-weight: 600;
  color: #eaf0f7;
}
@media screen and (max-width: 767px) {
  .footer__copyright {
    font-weight: 400;
  }
}

.fv {
  height: 31.25rem;
  background: #000;
}

.is-fixed {
  overflow: hidden;
}

.header {
  background: #fff;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 3000;
  border-top: 1rem solid #1d61af;
}

.header.is-clicked {
  background: #1d61af;
}

.header__inner {
  max-width: 92.5rem;
  margin-inline: auto;
  height: 6.25rem;
  padding-inline: 2.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 767px) {
  .header__inner {
    height: 5.625rem;
    padding-inline: 2.5rem;
  }
}

.header__title {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.9375rem;
}

.header__logo {
  transition: opacity 0.2s ease-out;
}

.header__logo:hover {
  opacity: 0.4;
}

.header__logo img {
  width: 9rem;
}
@media screen and (max-width: 767px) {
  .header__logo img {
    width: 6.25rem;
  }
}

.header__title-text {
  font-family: "Murecho", serif;
  font-size: 0.875rem;
  transition: opacity 0.2s ease-out;
}

.header__title-text:hover {
  opacity: 0.4;
}

.header__nav {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2.5rem;
}
@media screen and (max-width: 1440px) {
  .header__nav {
    gap: 1.875rem;
  }
}
@media screen and (max-width: 1023px) {
  .header__nav {
    gap: 1.5625rem;
  }
}

.header__nav-item {
  height: 100%;
  position: relative;
  display: grid;
  place-items: center;
}

.header__link {
  height: 100%;
  font-family: "Murecho", serif;
  font-size: 1.0625rem;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: color 0.2s ease-out;
}

.header__link:hover {
  color: #2c81d8;
}

.header__link-contact {
  font-family: "Murecho", serif;
  font-size: 1.0625rem;
  font-weight: 500;
  background: #569adf;
  color: #fff;
  padding-block: 0.375rem;
  padding-inline: 0.75rem;
  border-radius: 1.25rem 0 1.25rem 0;
  transition: opacity 0.2s ease-out;
}

.header__link-contact:hover {
  opacity: 0.4;
}

.header__btn {
  width: 2rem;
  height: 1.5rem;
  position: relative;
  z-index: 100;
  transition: width 0.3s ease-out;
}
@media screen and (max-width: 767px) {
  .header__btn {
    display: none;
  }
}

.header__btn.is-clicked {
  width: 2.5rem;
}

.header__btn.is-clicked .header__bar:nth-of-type(1),
.header__btn.is-clicked .header__bar:nth-of-type(3) {
  top: 0.75rem;
}

.header__btn.is-clicked .header__bar:nth-of-type(1) {
  transform: rotate(36.87deg);
}

.header__btn.is-clicked .header__bar:nth-of-type(2) {
  opacity: 0;
}

.header__btn.is-clicked .header__bar:nth-of-type(3) {
  transform: rotate(-36.87deg);
}

.header__bar {
  display: inline-block;
  width: 100%;
  height: 0.0625rem;
  border-radius: 0.375rem;
  background: red;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transition: transform 0.3s ease-out, top 0.3s ease-out, opacity 0.2s ease-out;
}

.header__bar:nth-of-type(2) {
  top: 0.75rem;
}

.header__bar:nth-of-type(3) {
  top: 1.5rem;
}

.inner {
  max-width: 1280px;
  padding-right: 40px;
  padding-left: 40px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (max-width: 767px) {
  .inner {
    padding-right: 25px;
    padding-left: 25px;
  }
}

.sub-menu {
  display: none;
  min-width: 12.5rem;
  background: #fff;
  border-top: 4px solid #569adf;
  position: absolute; /* サブメニューを親要素から絶対配置する */
  top: 100%; /* 親メニューの下に配置 */
  left: 50%;
  transform: translateX(-50%);
  z-index: 800;
}

.header__nav-item:hover .sub-menu {
  display: block;
}
@media screen and (max-width: 767px) {
  .header__nav-item:hover .sub-menu {
    display: none;
  }
}

.sub-menu__items {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.sub-menu__item {
  display: block;
  width: 100%;
  text-align: left;
  padding-block: 1rem;
  padding-left: 1.125rem;
  padding-right: 0.5rem;
  color: #1e1e1e;
  font-size: 0.9375rem;
  font-weight: 400;
  transition: all 0.2s ease-out;
  position: relative;
}

.sub-menu__item::after {
  content: "";
  width: 86%;
  height: 1px;
  background: repeating-linear-gradient(to right, black, black 1px, transparent 1.5px, transparent 5px);
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  transition: width 0.2s ease-out;
}

.sub-menu__item:last-of-type:after {
  display: none;
}

.sub-menu__item:hover {
  background: #e6eef7;
  color: #2c81d8;
}

.top-btn {
  aspect-ratio: 1/1;
  width: 3.9375rem;
  height: 3.9375rem;
  border-radius: 50%;
  background: #5d6775;
  transition: opacity 0.2s ease-out;
  position: fixed;
  bottom: 2.5rem;
  right: 2.1875rem;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  display: grid;
  place-items: center;
}
@media screen and (max-width: 767px) {
  .top-btn {
    bottom: 1.875rem;
    right: 0.5rem;
  }
}

.top-btn.is-show {
  opacity: 1;
  visibility: visible;
}

.top-btn:hover {
  opacity: 0.7;
}

.top-btn__wrap {
  display: grid;
  place-items: center;
}

.top-btn__arrow {
  display: inline-block;
  width: 0.875rem;
  height: 0.875rem;
  border-top: 2px solid #fff;
  border-left: 2px solid #fff;
  border-radius: 1px;
  transform: rotate(45deg);
}

.top-btn__text {
  font-size: 0.7875rem;
  color: #fff;
}